*{
    color: #D6C5F0;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    border: none;
}

body{    
    font-family: 'Poppins', sans-serif;
    background-color: #1B0044;
    overflow-x: hidden;
}

h1{
    font-size: 3.8vw;
}

h2{
    font-size: 1.9vw;
}

h3{
    font-size: 1.9vw;
}

p{
    font-size: 1.1vw;
}

a{
    text-decoration: none;
    color: #FF8F6F;
    transition: 0.3s;
}

button{
    background-color: #167b9d00;
    height: 3.5vw;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding-left: 3vw;
    padding-right: 3vw;
    font-size: 1.1vw;
    border-radius: 40px;
    border: 2px solid #9153F4;
    color: #9153F4; 
    cursor: pointer;
    transition: 0.3s;
}

button:hover{
    background-color: #9153F4;
    color: #D6C5F0;
}

a:hover{
    color: #167B9D;
}

.intro{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height:100vh;
    width:100vw;
    background-image: url(/static/media/intro-wave.6d28e0cf.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.introName{
    width: 55%;
    padding-left: 5vw;
    padding-right: 5vw;
    padding-top: 32vh;
    height: 100%;
}

.introImg{
    width: 45%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding-bottom: 18vh;
}

.introImg > img{
    width: 75%;
}

.about{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-color: #5727A3;
}

.about > span{
    height: 20%;
    display: flex;
    align-items: center;
}

.about-content{
    padding-top:4vw;
    display: flex;
}

.about-text{
    width: 50%;
    padding-left: 8vw;
    padding-right: 5vw;
    border-right: 2px solid #D6C5F0;
}

.about-img{
    width: 50%;
    padding-left: 5vw;
    padding-right: 18vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: space-between;
}

.about-row{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.about-row > img{
    width: 15%;
}

.sql{
    width:20% !important;
}

.java{
    width:16% !important;
}

.firstWaveSep{
    aspect-ratio: 960/300;
    background-image: url(/static/media/layered-waves-haikei.0ba0cea2.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -4px;
    margin-bottom: -4px;
    margin-left: -4px;
    margin-right: -4px
}

.firstWaveSep > span{
    height: 20%;
    display: flex;
    align-items: center;
    padding-top: 8%;
}

.projects{
    height: 40vw;
    position: relative;
    background-color: #D6C5F0;
}

.proj-image{
    position: absolute;
    width:30vw;
    left:14vw;
    top: 4vw;
}

.proj-text{
    position: absolute;
    width:45vw;
    border-radius: 40px;
    left: 48vw;
    top: 7vw;
    z-index: 10;
    background-color: #00000000;
}

.proj-text > h2{
    color: #9153F4 !important;
    font-size: 2.3vw !important;
}

.proj-text-desc{
    padding-top: 1vw;
    padding-bottom: 2vw;
    padding-right:10vw;
}

.proj-text-desc > p{
    color: #9153F4 !important;
}

.secondWaveSep{
    aspect-ratio: 960/300;
    background-image: url(/static/media/layered-waves-haikei-4.2f5e08c0.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -4px;
    margin-bottom: -4px;
    margin-left: -4px;
    margin-right: -4px
}

.secondWaveSep > span{
    height: 20%;
    display: flex;
    align-items: center;
}

.platforms{
    height: 15vw;
    background-color: #5727A3;
    display: flex;
    align-items: center;
    padding-bottom: 10vw;
}

.plat-icons{
    width: 100%;
    display: flex;
    justify-content: space-around;
    padding-left: 25vw;
    padding-right: 25vw;
}

.plat-icons > a{
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: default;
    width: 15%;
}

.git > img, .linked > img{
    width: 7vw;
}

.email > img{
    width: 9vw;
}

.plat-icons > a > img{
    transition: 0.3s;
    cursor: pointer !important;
}

.git > img:hover, .linked > img:hover{
    width: 8vw;
}

.email > img:hover{
    width: 10vw;
}

.footer{
    background-color: #5727A3;
    display: flex;
    justify-content: center;
}

.footer > p{
    font-size: 1vw;
    padding: 1vw;
}



